.product-card {
  box-sizing: border-box;
  padding: 32px 16px 16px;
  width: 200px;
  height: 406px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
}

.product-card__img {
  margin: 0 3px 36px;
}

.product-img {
  width: 100%;
  height: 134px;
}

.product-card__discription {
  margin-bottom: 16px;
}

.product-card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  color: var(--main-color);
}

.product-card__card-id {
  font-size: 10px;
  line-height: 14px;
  color: var(--secondary-color);
}

.product-card__rating {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.product-card__rewiew {
  font-size: 10px;
  line-height: 14px;
  color: var(--main-color);
}

.product-card__price {
  margin-bottom: 16px;
}

.price {
  display: flex;
  justify-content: space-between;
}

.price-name {
  font-size: 12px;
  line-height: 18px;
  color: var(--secondary-color);
}

.price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  color: var(--main-color);
}

.product-card__button:link {
  display: flex;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #fff;
  width: 166px;
  height: 40px;
  background: var(--blue-accent);
  border-radius: 5px;
  text-decoration: none;
}

.product-card__button:visited {
  color: #fff;
}

.product-card__button:hover {
  color: var(--blue-accent);
  background-color: #fff;
  border: 1px solid var(--blue-accent);
  border-radius: 5px;
  transition: 0.5s;
}

.product-card__button:active {
  color: var(--blue-accent);
  opacity: 80%;
}
